*{
  font-family: Helvetica,Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  padding: 60px;
}
input{
  font-size: 1.2rem;
  color: gray;
  margin: 5px 0px;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-right: 10px;
  max-width: 400px;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-width:100%;
}
